import { useReducer } from "react";
import { prevDefinedSegs } from "../../utils/data/pre-defined-vals";
import Button from "../UI/Buttons/Button";
import headerClasses from "./Header.module.css";

const backGroundReducer = (prevState, dispach) => {
  const { name, action } = dispach;
  if (action === "hover" && !prevState.click) return { name, click: false };
  return { name, click: true };
};

const Header = () => {
  const [bkgState, dispatchBkg] = useReducer(backGroundReducer, {
    name: "default",
    click: false,
  });

  const hoverHandler = (name) => {
    console.log(name);
    dispatchBkg({ name, action: "hover" });
  };

  const clickHandler = (name) => {
    console.log("Clicked");
    dispatchBkg({ name, action: "clicked" });
  };

  const Buttons = prevDefinedSegs.map((el) => (
    <Button
      key={el.id}
      linkTo={el.ref}
      onHover={hoverHandler}
      onClick={clickHandler}
    >
      {el.name}
    </Button>
  ));
  return (
    <nav className={headerClasses.header}>
      <div className={headerClasses.linkContainer}>{Buttons}</div>
    </nav>
  );
};

export default Header;
