import { useReducer } from "react";
import Control from "./components/Control/Control";
import Header from "./components/Header/Header";
import Section from "./components/Sections/Section";
import appClasses from "./App.module.css";
import Footer from "./components/Footer/Footer";
// import "antd/dist/antd.css";

const backGroundReducer = (prevState, dispach) => {
  const { name, action } = dispach;
  if (action === "hover" && !prevState.click) return { name, click: false };
  return { name, click: true };
};

function App() {
  const [bkgState, dispatchBkg] = useReducer(backGroundReducer, {
    name: "default",
    click: false,
  });

  const hoverHandler = (name) => {
    console.log(name);
    dispatchBkg({ name, action: "hover" });
  };

  const clickHandler = (name) => {
    console.log("Clicked");
    dispatchBkg({ name, action: "clicked" });
  };

  const isHover = !bkgState.click && bkgState.name !== "default";

  const classForApp = !bkgState.click
    ? `${appClasses.App} `
    : `${appClasses.click}`;

  return (
    <div className={`${classForApp} ${isHover && appClasses.isHover}`}>
      {bkgState.click && (
        <Header onHover={hoverHandler} onClick={clickHandler} />
      )}
      {!bkgState.click && (
        <Control onHover={hoverHandler} onClick={clickHandler} />
      )}
      {bkgState.click && <Section />}

      <Footer></Footer>
    </div>
  );
}

export default App;
