import classes from "./Control.module.css";
import Button from "../UI/Buttons/Button";
import { prevDefinedSegs } from "../../utils/data/pre-defined-vals";

const Control = (props) => {
  const Buttons = prevDefinedSegs.map((el) => (
    <Button
      key={el.id}
      linkTo={el.ref}
      onHover={props.onHover}
      onClick={props.onClick}
    >
      {el.name}
    </Button>
  ));

  return (
    <div className={classes.control}>
      <div className={classes.textBlock}>
        <h1>Hi, I am Lu Chen</h1>
        <h3>I love programming</h3>
      </div>
      <div className={classes.bottomContainer}>{Buttons}</div>
    </div>
  );
};

export default Control;
